$columns: 12;
$column-base-width: 100% / $columns;
 
* {
    box-sizing : border-box;
  }
   
  [class*='col-'] {
    min-height: 1px;
    width: $column-base-width;
    padding: 12px;
  }
  
@for $i from 1 through $columns {
    .col-#{$i} {
      width: $column-base-width * $i;
    }
  }

  .container {
    width: 100%;
    max-width: 100vw;
    margin: 0 auto;
    padding: 0 15px;
  }
  .row {
    display: flex;
    flex-wrap: wrap;
  }

  